<template>
	<div id="my" >
		<div id="userInfo">      <!-- @click='showMyInfo=true' -->
			<div class="userBox" @click='changeComponent("myInfo")' >
				<img src="http://img2.imgtn.bdimg.com/it/u=3363472252,3522967664&fm=26&gp=0.jpg" alt="" >
				<div>
					<p>{{myName}}</p>
					<span>微信号：Maple_Story<img src='~img/images/my/erweima.png'></span>
				</div>
			</div>
			<img src="~img/images/my/camera.png" alt="" class='camera'>
		</div>
		<ul class='findList'>
			<li v-for='(val,index) in list' @click='changeComponent(val.cname)' >
				<img :src="val.img" alt="">
				<p>{{val.name}}</p>
			</li>
		</ul>
		<van-popup
			  v-model="showPop"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
				:duration=0.2
			>
			<component v-bind:is="comName" :ref='comName' @isShow='isShow' @cName='changeName' ></component>
		</van-popup>
	</div>
</template>
<script>
	import myInfo from '@/components/myInfo'
	import pay from '@/components/pay'
	import myCollection from '@/components/myCollection'
	import album from '@/components/album'
	import mySet from '@/components/set'
	export default{
		name:'my',
		data(){
			return{
				showMyInfo:false,
				showPay:false,
				showCollection:false,
				showAlbum:false,
				showSet:false,
				myName:'枫叶回忆',
				comName:'',
				showPop:false,
				list:[
					{
						name:'支付',
						cname:'pay',
						img:'https://cdn.u1.huluxia.com/g3/M03/51/CD/wKgBOV3Xl2GAff1iAAAEMWuyILM515.png'
					},
					{
						name:'收藏',
						cname:'myCollection',
						img:'https://cdn.u1.huluxia.com/g3/M03/51/CE/wKgBOV3Xl4OAHaloAAAEKDEIVAE621.png'
					},
					{
						name:'相册',
						cname:'album',
						img:'https://cdn.u1.huluxia.com/g3/M03/51/CE/wKgBOV3Xl5SAXMUbAAAELzHvfCM514.png'
					},
					{
						name:'卡包',
						img:'https://cdn.u1.huluxia.com/g3/M03/51/CE/wKgBOV3Xl6CAPvu7AAACnaCMyLc340.png'
					},
					{
						name:'表情',
						img:'https://cdn.u1.huluxia.com/g3/M03/51/CE/wKgBOV3Xl6uAfhzWAAAEyVa-ST0373.png'
					},
					{
						name:'设置',
						cname:'mySet',
						img:'https://cdn.u1.huluxia.com/g3/M03/6E/0D/wKgBOV3otmuAVaurAAAFoa-Hh0o537.png'
					}
				]
			}
		},
		mounted(){
			sessionStorage.setItem('myName',this.myName);
		},
		methods:{
			changeComponent(comName){
				if(comName){
					this.comName=comName;
					this.showPop=true;
				}
			},
			isShow(){
				this.showPop=false;
			},
			changeName(value){
				this.myName=value;
			}
		},
		beforeRouteLeave (to, from, next) {
			if(this.$refs.myInfo && this.$refs.myInfo.showImg){
				this.showPop=true;
				this.$refs.myInfo.showImg=false;
				if(this.$refs.myInfo.showPop){
					this.$refs.myInfo.showImg=true;
					this.$refs.myInfo.showPop=false;
				}
				next(false);
		    }	
		    else if(this.$refs.myInfo && this.$refs.myInfo.changeName){
				this.showPop=true;
				this.$refs.myInfo.leaveName();
				next(false);
		    }
		    else if(this.$refs.pay && this.$refs.pay.showpayManagement){
				this.$refs.pay.showpayManagement=false;
				this.showPop=true;
				next(false)
		    }	
		    else if(this.$refs.album){
				this.showPop=false;
				this.$refs.album.active=0;
				next(false);
			}
			else if(this.showPop && to.name!='myFriendsCircle'){
				this.showPop=false;
				next(false);
			}
			else{
				next();
			}
	    
		},
		beforeRouteEnter (to, from, next) {
			if(from.name==='myFriendsCircle'){
				next(vm=>{
					vm.showPop=true;
				});

			}
			else{
				next();
			}

		},
		components:{
			myInfo,pay,myCollection,album,mySet
		}
	}
</script>
<style scoped>
	#my{
		background: #ededed;
		height: 93vh;
	}
	#userInfo{
		padding: .78rem .2rem;
		background: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-top: .9rem;
	}
	.userBox{
		height: 50%;
		width: 92%;
		display: flex;
		align-items: center;
		position: relative;
		margin-top: .4rem;
	}
	.userBox>img{
		width: 1.22rem;
		height: 1.22rem;
		border-radius: .16rem;
	}
	.userBox>div{
		margin-left: .3rem;
		display: flex;
		width: 100%;
		flex-direction: column;
	}
	.userBox>div>p{
		font-size: .4rem;
		margin-bottom: .1rem;
		font-weight: 600;
		width: 81%;
	}
	.userBox>div>span{
		font-size: .33rem;
		color: #7f7f7f;
		background: #fff url('~img/images/right.png') no-repeat right center/.3rem;
		display: flex;
		align-items: center;
	}
	.userBox>div>span>img{
		width: .36rem;
	    height: .36rem;
	    position: absolute;
	    right: .6rem;

	}
	.camera{
		position: absolute;
	    top: .27rem;
	    right: .4rem;
	    width: .5rem;
	}
	.findList{
		margin-top: .2rem;	
		margin-bottom: 1.9rem;	
	}
	.findList>li{
		background: #fff;	
		padding:.3rem;
		padding-right: 0;
		display: flex;
		align-items: center;
		background: #fff url('~img/images/right.png') no-repeat 96% center/.3rem;
	}
	.findList>li>img{
		width: .52rem;
    	height: .52rem;
	}
	.findList>li>p{
		font-size: .38rem;
    	margin-left: .2rem;
    	
	}
	.findList>li:nth-of-type(1),.findList>li:nth-of-type(5){
		margin-bottom: .2rem;		
	}
	.borBottom p:after ,.borBottom p:after,.borBottom p:after{
		content: '';
		display: block;
		width: 86vw;
		position: relative;
		top:.3rem;
	    border-bottom:1px solid rgba(233,233,233,.6);
	}
	.van-image-preview {
		background: rgba(0,0,0,.8);
	}
</style>


